.Stats {
    width: 100%;
    display: grid;
    grid-template-columns: 12.5% 37.5% 37.5% 12.5%;
    font-size: 1.7em;
}

.event {
    display: grid;
    grid-template-rows: 25% 75%;
    -ms-flex-pack: space-evenly;
        justify-content: space-evenly;
    padding: .5em;
}

.eventnum {
    padding: .5em;
}

.heading {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
}

.headingwins {
    color: rgb(24, 163, 24);

}

.heading div {
    padding: .5em;
}



